/* -----Icons */
@font-face {
  font-family: 'icomoon';
  src: url('./images/icons/icomoon.eot?poemc3');
  src: url('./images/icons/icomoon.eot?poemc3#iefix')
      format('embedded-opentype'),
    url('./images/icons/icomoon.ttf?poemc3') format('truetype'),
    url('./images/icons/icomoon.woff?poemc3') format('woff'),
    url('./images/icons/icomoon.svg?poemc3#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^='icon-'],
[class*=' icon-'] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-pencil:before {
  content: '\e905';
}
.icon-heart:before {
  content: '\e9da';
}
.icon-code:before {
  content: '\ea7f';
}
.icon-twitter:before {
  content: '\ea96';
}
.icon-github:before {
  content: '\eab0';
}

/* -----Styles */

body {
  margin: 0;
  font-family: -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto',
    'Helvetica', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji',
    'Segoe UI Symbol';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  color: rgb(255, 152, 0);
  text-decoration: none;
  transition: all 0.1s ease 0s;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

